<template>
  <div class="users-container">
    <div style="display: flex; justify-content: space-between;">
      <div style="display: flex;">
        <el-input
          style="border-radius: 4px;"
          placeholder="Search"
          prefix-icon="el-icon-search"
          v-model="searchKeyword"
        ></el-input>

        <el-select
          v-model="statusType"
          placeholder="Select"
          style="margin-left: 10px"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <el-button
        style="background: #0f3959; color: white;"
        icon="el-icon-plus"
        @click="openNewIconDialog"
        >New Icon</el-button
      >
    </div>
    <el-table
      :data="iconList"
      border
      style="width: 100%; margin-top: 10px;"
      :header-cell-style="headerCellStyle"
      @row-click="onClickRow"
    >
      <el-table-column sortable label="Icons" width="180">
        <template slot-scope="scope">
          <img
            :src="scope.row.filePath | filePath"
            alt=""
            style="width: 30px; height: 30px;"
          />
        </template>
      </el-table-column>
      <el-table-column sortable label="Status">
        <template slot-scope="scope">
          {{ scope.row.status ? "Active" : "Deactive" }}
        </template>
      </el-table-column>
      <el-table-column sortable prop="name" label="Application Name">
      </el-table-column>
      <el-table-column prop="path" label="File Path"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      searchKeyword: "",
      options: [
        {
          value: -1,
          label: "Show All",
        },
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
      statusType: -1,
    };
  },
  methods: {
    headerCellStyle() {
      return {
        background: "#0f3959",
        color: "white",
        fontWeight: "normal",
      };
    },
    openNewIconDialog() {
      this.$store.commit("setNewIconDialogVisibility", true);
    },
    onClickRow(object) {
      this.$store.commit("icon/setUpdateIcon", object);
      this.$store.commit("setEditIconDialogVisibility", true);
    },
  },
  mounted() {
    this.$store.dispatch("icon/loadList", { notify: this.$notify });
  },
  computed: {
    ...mapState("icon", ["list"]),
    iconList() {
      let icons = [];
      this.list.forEach((icon) => {
        if (icon.name.toLowerCase().includes(this.searchKeyword)) {
          if (this.statusType > -1) {
            if (icon.status == this.statusType) {
              icons.push(icon);
            }
          } else {
            icons.push(icon);
          }
        }
      });
      return icons;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/users.scss";
</style>
